import React from "react"
import Draw from "../components/Draw"
import Photo from "../components/Photo"

import image from "../images/1.png"

const Stoicy = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Cytat z książki"
				title='Na podstawie książki został stworzony film: "Siła spokoju"'
			/>
			<h2>Trudno jest być stoikiem</h2>
			<article>
				W stoicyzmie jest moc. Podoba mi się ta filozofia, jednoczęśnie
				staram się praktykować w codziennych trudnościach.
			</article>
			<Draw type="stoic" />
		</>
	)
}

export default Stoicy
