import React from "react"
import Draw from "../components/Draw"
import Photo from "../components/Photo"

import image from "../images/4.png"

const Work = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Cytat Seneca"
				title="Albo zwyciężasz albo się uczysz"
			/>
			<h2>Dalejże kowalu</h2>
			<article>Nie wiem jaki rodzaj</article>
			<Draw type="work" />
		</>
	)
}

export default Work
