import React from "react"
import Draw from "../components/Draw"
import Photo from "../components/Photo"

import image from "../images/7.png"

const Filozofy = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Cytat o samopoczuciu"
				title="Możesz mieć nad tym kontrole"
			/>
			<h2>Filozoficzne</h2>
			<article>
				Struktura determinuje funkcję dzięki temu możemy czerpać wiedzę
				z ubiegłych pokoleń bo: BĘDZIE DZIAŁAĆ
			</article>
			<Draw type="filozofy" />
		</>
	)
}

export default Filozofy
