import React from "react"
import Draw from "../components/Draw"
import Photo from "../components/Photo"

import image from "../images/3.png"

const MainPage = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Cytat fotografia"
				title="Nie trzeba być KIMŚ żeby tworzyć"
			/>
			<h2>Co to za aplikacja?</h2>
			<article>
				Głównym powodem stworzenia tej strony była chęć podnoszenia
				swoich umiejętności jako programista a przy okazji chciałbym
				przedstawić Ci kilkadziesiąt cytatów na które trafiłem
				poczytując książki, śledząc twórców a przedewszystkim
				interesując się samorozwojem, uważam że większość z nich mają
				nad przeciętny przekaz a nawet w niektórych podaję swoją{" "}
				<strong>sugestje</strong> do sposobu intrepretacji
			</article>
			<Draw title="Wylosuj coś dla siebie!" type="all" />
		</>
	)
}

export default MainPage
