import React from "react"
import Draw from "../components/Draw"

import Photo from "../components/Photo"

import image from "../images/2.png"

const Books = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Cytat z książki"
				title='Na podstawie książki został stworzony film: "Siła spokoju"'
			/>
			<h2>Cytaty z książek</h2>

			<article>
				Zawieram tu cytaty głównie z książek które sam gdzieś kiedyś
				przeczytałem a te fragmenty zrobiły na mnie wrażenie. Tytuły są
				na <a href="/#">TEJ STRONIE</a>
			</article>
			<Draw type="book" />
		</>
	)
}

export default Books
