import React from "react"

import Photo from "../components/Photo"

import image from "../images/6.png"

const ErrorPage = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Błąd strony"
				title="Spróbuj się cofnąć"
			/>
			<h2>Coś poszło nie tak...</h2>
		</>
	)
}

export default ErrorPage
