import React from "react"
import Draw from "../components/Draw"
import Photo from "../components/Photo"

import image from "../images/5.png"

const Insta = () => {
	return (
		<>
			<Photo
				src={image}
				shortTitle="Widok instagrama"
				title="Tam są cytaty w ładnych szablonach"
			/>
			<h2>Z mojego insta</h2>
			<article>
				Tworzyłem przez pewnien czas grafiki z insta w formie złotych
				myśli/cytatów myślę że jest to wartę uwagi:{" "}
				<a href="/#">PROFIL</a>
			</article>
			<Draw type="insta" />
		</>
	)
}

export default Insta
